.side-widget {
  display: flex;
  align-items: flex-end;
  justify-content: center;

  max-width: 150px;
  min-height: 100vh;

  position: fixed;
  left: 0;
  bottom: 0;

  background-color: rgba(51, 51, 51, 0.75);
  @include border;

  @media (max-width: calc(768px + 30px)) {
    display: none;
  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    max-width: 150px;

    padding-bottom: 25px;
    @include border;
  }

  &__title {
    display: flex;
    align-items: center;

    position: relative;
    transform: rotate(-90deg);
    bottom: 205px;
    width: max-content;

    font-family: "OpensansCondensedBold-700", sans-serif;
    font-size: 18px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

    color: $thirdColor;

    &::before {
      margin-right: 40px;
      content: '';
      display: inline-block;
      background-color: $thirdColor;
      height: 2px;
      width: 80px;
    }
  }

  &__items {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    max-width: max-content;
  }

  &__item {
    display: flex;
    max-width: max-content;
    min-width: 30px;
    height: 30px;

    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }

    &:hover {
      cursor: pointer;
    }

    &--instagram {
      background: url(../images/side-widget/instagram.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;

      transition: all $tranDur;

      &:hover, :focus {
        background: url(../images/side-widget/instagram-active.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }

    &--facebook {
      background: url(../images/side-widget/facebook.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;

      transition: all $tranDur;

      &:hover, :focus {
        background: url(../images/side-widget/facebook-active.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }

    &--vk {
      background: url(../images/side-widget/vk.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;

      transition: all $tranDur;

      height: 20px;
      width: 30px;

      &:hover, :focus {
        background: url(../images/side-widget/vk-active.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }

    &--youtube {
      background: url(../images/side-widget/youtube.svg);
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;

      transition: all $tranDur;

      height: 22px;

      &:hover, :focus {
        background: url(../images/side-widget/youtube-active.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
  }
}