.btn {
  &__wrap {
    @media (max-width: calc(576px + 30px)) {
      text-align: center;
    }
  }

  width: 160px;
  height: 50px;
  background-color: $secondColor;
  font-family: "OpensansCondensedBold-700";
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  border: solid 2px transparent;

  transition: border $tranDur,
  background-color $tranDur,
  color $tranDur;

  &:hover {
    background-color: transparent;
    color: $fourthColor;
    border: solid 2px $fourthColor;
    cursor: pointer;
  }
}