.container {
  margin: 0 auto;
  padding: 0 15px;

  // @media (max-width: calc(5000px + 30px)) {
  //   max-width: 1110px;
  //   border: solid 1px magenta;
  // }

  @media (max-width: calc(1920px + 30px)) {
    max-width: 1110px;
    border: solid 1px yellow;
  }

  @media (min-width: calc(1920px + 30px)) {
    max-width: 1110px;
    border: solid 1px magenta;
  }

  @media (max-width: calc(1410px + 30px)) {
    margin-left: 150px;
  }

  @media (max-width: calc(992px + 30px)) {
    max-width: 100%;
    // padding: 0 20px;
    // margin-left: calc(150px + 40px);
    border: solid 1px blue;
  }

  @media (max-width: calc(768px + 30px)) {
    max-width: 690px;
    border: solid 1px green;
  }

  @media (max-width: calc(768px + 30px)) {
    margin: 0 auto;
  }
  
  @media (max-width: calc(576px + 30px)) {
    max-width: 510px;
    border: solid 1px cyan;
  }

}